const up = '<i class="fas fa-angle-double-up"></i>';
const down = '<i class="fas fa-angle-double-down"></i>';
export default function applyHooks() {
    var menus = document.getElementsByClassName("navdrop-content");
    for (var k = 0; k < menus.length; k++) {
        let root = menus[k];
        if (root.dataset.processedNavHints == "true")
            continue;
        //if (root.scrollHeight <= root.clientHeight)
        //continue;
        root.dataset.processedNavHints = "true";
        var entries = root.getElementsByClassName("navdrop-entry");
        let first = entries[0];
        var entries2 = root.getElementsByClassName("navdropdown-subheader");
        if (entries2.length > 0)
            first = entries2[0];
        createNavScroller(root, "up", up, first);
        createNavScroller(root, "down", down, entries[entries.length - 1]);
        let overrun = root.scrollHeight - root.clientHeight;
        //console.log("Menu "+root+" has "+overrun+"px of overrun");
        if (overrun > 0) {
            for (var i = 0; i < entries.length; i++) {
                let entry = entries[i];
                entry.style.paddingLeft = "0.4rem";
            }
        }
        processVisibilityChanges(root, "up", true);
    }
}
function handleNavEntryVisibilityChange(entries, direction) {
    let visible = false;
    let root = null;
    for (var i = 0; i < entries.length; i++) {
        visible = entries[i].isIntersecting;
        root = entries[i].target.parentNode;
        if (visible)
            break;
    }
    if (root == null)
        return;
    processVisibilityChanges(root, direction, visible /* || window.getComputedStyle(root).display == 'none'*/);
}
function processVisibilityChanges(root, direction, visible) {
    let seek = "nav-scroller-" + direction;
    var list = root.getElementsByClassName(seek);
    for (var i = 0; i < list.length; i++) {
        let elem = list[i];
        elem.style.opacity = visible ? "0" : "1";
        //console.log("Visibility is "+visible+"; setting "+direction+" arrows to "+elem.style.opacity+" ("+window.getComputedStyle(root).display+")");
    }
}
function createNavScroller(pane, dir, content, ref) {
    let observer = new IntersectionObserver(function (entries) { handleNavEntryVisibilityChange(entries, dir); }, { threshold: 0.875, root: pane });
    let elem = document.createElement("div");
    elem.className = "nav-scroller-" + dir;
    elem.classList.add("nav-scroller");
    elem.innerHTML = content + content;
    pane.insertBefore(elem, ref);
    observer.observe(ref);
    return elem;
}
